.product {
  box-sizing: border-box;
  flex-direction: column;
  width: 200px;
  margin: 0 auto;
  padding: 32px 16px 16px;
  display: flex;
}

.product__image {
  margin-inline: 3px;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  display: block;
}

.product__title {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.product__buy {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  align-self: center;
  width: 166px;
  height: 40px;
  margin-top: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.product__buy:hover {
  color: #00acdc;
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.rating__stars {
  gap: 4px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.rating__star {
  background-image: url("star.822d561e.svg");
  background-position: 0;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0;
  padding: 0;
  display: block;
}

.rating__stars :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-position: 0;
  background-repeat: no-repeat;
}

.rating__reviews {
  margin: 0;
  padding: 0;
  font-size: 10px;
  line-height: 14px;
}

.price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.price__title {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.price__value {
  margin: 0;
  font-size: 16px;
  line-height: 18px;
}

body {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
}
